import { FormikBag } from "formik"

import {
  checkValueError,
  validateEmail,
  validateRequired,
} from 'utils/validation/validation'
import { FormFields, LoginFormProps, LoginFormValues, OtherProps } from "pages/Login/LoginTypes.d"
import { ObjField } from "constants/types"
import { convertValidateField } from "utils/MyUtils"

export const listFields: ObjField = {
  email: {
    fieldName: 'email',
    validationRules: [
      { rule: 'isRequired', code: 'FORM_ERROR_REQUIRED_FIELD', validator: validateRequired },
      { rule: 'isEmail', code: 'FORM_ERROR_REQUIRED_FIELD_EMAIL_INVALID', validator: validateEmail },
    ],
    fieldInput: 'input',
    fieldLabel: 'Email',
    fieldValue: '',
    fieldType: 'email',
  },
  password: {
    fieldName: 'password',
    validationRules: [
      { rule: 'isRequired', code: 'FORM_ERROR_REQUIRED_FIELD', validator: validateRequired },
    ],
    fieldInput: 'input',
    fieldLabel: 'Password',
    fieldValue: '',
    fieldType: 'password',
  },
}

export const onSubmit = async (
  values: LoginFormValues,
  {
    setErrors,
    props,
    setSubmitting,
  }: FormikBag<LoginFormProps & OtherProps, LoginFormValues>
) => {
  setSubmitting(true)

  try {
    await props.login(values)
  } catch (error) {
    setErrors(error.data)
  } finally {
    setSubmitting(false)
  }
}

export const formikAction = {
  mapPropsToValues: (props: FormFields) => {
    return {
      email: props.formFields.email.fieldValue,
      password: props.formFields.password.fieldValue,
    }
  },
  validate: checkValueError(convertValidateField(listFields)),
  handleSubmit: onSubmit,
}