import { FormikBag } from 'formik'

import { RegisterFormValues, OtherProps } from 'pages/Register/RegisterTypes.d'

import { checkValueError, validateRequired } from 'utils/validation/validation'
import { convertValidateField } from 'utils/MyUtils'

import { ObjField } from 'constants/types'

export const listRegisterFields: ObjField = {
  email: {
    fieldName: 'email',
    validationRules: [
      { rule: 'isRequired', code: 'FORM_ERROR_REQUIRED_FIELD', validator: validateRequired },
    ],
    fieldInput: 'input',
    fieldLabel: 'Email',
    fieldValue: '',
    fieldType: 'email',
  },
  firstname: {
    fieldName: 'firstname',
    validationRules: [
      { rule: 'isRequired', code: 'FORM_ERROR_REQUIRED_FIELD', validator: validateRequired },
    ],
    fieldInput: 'input',
    fieldLabel: 'First Name',
    fieldValue: '',
    fieldType: 'text',
  },
  lastname: {
    fieldName: 'lastname',
    validationRules: [
      { rule: 'isRequired', code: 'FORM_ERROR_REQUIRED_FIELD', validator: validateRequired },
    ],
    fieldInput: 'input',
    fieldLabel: 'Last Name',
    fieldValue: '',
    fieldType: 'text',
  },
}
export const listRegisterFieldsPassword: ObjField = {
  password: {
    fieldName: 'password',
    validationRules: [
      { rule: 'isRequired', code: 'FORM_ERROR_REQUIRED_FIELD', validator: validateRequired },
    ],
    fieldInput: 'input',
    fieldLabel: 'Password',
    fieldValue: '',
    fieldType: 'password',
  },
  confirmPassword: {
    fieldName: 'confirmPassword',
    validationRules: [
      { rule: 'isRequired', code: 'FORM_ERROR_REQUIRED_FIELD', validator: validateRequired },
    ],
    fieldInput: 'input',
    fieldLabel: 'Confirm Password',
    fieldValue: '',
    fieldType: 'password',
  },
}

export const onSubmit = async (
  values: RegisterFormValues,
  { setErrors, props, setSubmitting }: FormikBag<OtherProps, RegisterFormValues>
) => {
  setSubmitting(true)

  try {
    await props.handleSubmit(values)
  } catch (error) {
    setErrors(error.data)
  } finally {
    setSubmitting(false)
  }
}

export const formikAction = {
  mapPropsToValues: () => {
    return {
      email: '',
      firstname: '',
      lastname: '',
      password: '',
    }
  },
  validate: checkValueError(
    convertValidateField({
      ...listRegisterFields,
      ...listRegisterFieldsPassword,
    })
  ),
  handleSubmit: onSubmit,
}
